<template>
  <div class="no-data">
    <van-icon name="warning-o" />
    <div class="text">{{text}}</div>
  </div>
</template>

<script>
  export default {
    props: ['text']
  }
</script>

<style scoped>
  .no-data {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 5vw 1vw;
  }

  .no-data .van-icon {
    font-size: 18vw;
    color: #aaa;
  }

  .no-data .text {
    color: #aaa;
    font-size: 5vw;
  }
</style>


