<template>
  <div>
    <van-nav-bar left-arrow left-text="返回" @click-left="$router.back()" title="购物车"></van-nav-bar>
    <div class="list">
      <van-button type="primary" @click="$store.showSignin = true" v-if="!$store.user.id" size="large">登录后体验更好</van-button>
      <template v-if="cart.length > 0">
        <van-checkbox class="card-goods__item" v-for="(item, index) in cart" :key="item.product_id"
          :name="item.product_id" v-model="item.checked" @change="handleSelect(item)">
          <van-card :title="item.name" :num="item.count" :price="item.price" :thumb="item.cover">
            <div slot="num" @click.stop>
              <van-stepper v-model="item.count" @plus="changeCount(item, index)" @minus="changeCount(item, index)" />
              <van-button type="danger" size="mini" icon="delete" class="trash-btn" @click="del(index)"
                v-if="$store.user"></van-button>
            </div>
          </van-card>
        </van-checkbox>
      </template>
      <no-data text="你的购物车为空" v-else />
    </div>

    <van-submit-bar :price="totalPrice" :disabled="!selectedList.length" button-text="结算" @submit="order">
      共{{productCounts}}件</van-submit-bar>
  </div>
</template>

<script>
  import noData from '../components/noData.vue'
  export default {
    data() {
      return {

      }
    },
    components:{
      'no-data':noData
    },
    computed: {
      productCounts() {
        let t = 0
        return this.selectedList.reduce((prev, curr, index) => {
          return t += curr.count
        }, t)
      },
      selectedList() {
        return this.cart.filter(item => item.checked)
      },
      cart() {
        return this.$store.cart
      },
      totalPrice() {
        let t = 0
        return this.selectedList.reduce((prev, item) => {
          return t += item.price * item.count
        }, t) * 100
      },
    },
    mounted() {
      this.$axios.get('/cart/all').then(res => {
        if (res.errcode === 0) {
          this.$store.cart = res.data
        }
      })
    },
    methods: {
      handleSelect(item) {
        this.$axios.post('/cart/update', {
          product_id: item.product_id,
          checked: item.checked ? 1 : 0
        }).then(res => {
          if (res.errcode === 0) {

          }
        })
      },
      del(index) {
        this.$axios.get('/cart/delete', {
          params: { product_id: this.cart[index].product_id }
        }).then(res => {
          if (res.errcode === 0) {
            this.$store.cart.splice(index, 1)
          } else if (res.errcode === 90101) {
            this.$store.cart.splice(index, 1)
            localStorage.cart = JSON.stringify(this.$store.cart)
          }
        })

      },
      changeCount(item, index) {
        this.$nextTick(() => {
          this.$axios.post('/cart/update', {
            product_id: item.product_id,
            count: item.count
          }).then(res => {
            if (res.errcode === 90111) {
              localStorage.cart = JSON.stringify(this.$store.cart)
            }
          })
        })
      },
      order() {
        sessionStorage.buyProducts = JSON.stringify(this.cart.filter(item => item.checked))
        this.$router.push({ name: 'orderConfirm' })
      }
    }

  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .cartlist {
    height: calc(100vh - 96px);
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    padding: 10px 0;
    background-color: #fff;
  }

  .trash-btn {
    font-size: 16px;
    height: 27px;
    min-width: 27px;
    margin-left: 63px;
    margin-top: 5px;
  }

  .card-goods__item {
    position: relative;
    background-color: #fafafa;
  }

  .van-checkbox__label {
    width: 100%;
    height: auto;
    padding: 0 10px 0 15px;
    box-sizing: border-box;
  }

  .van-checkbox__icon {
    top: 50%;
    left: 10px;
    z-index: 1;
    position: absolute;
    margin-top: -10px;
  }

  .van-card__price {
    color: #f44;
  }

  .van-submit-bar {
    padding-left: 1em;
  }
</style>


